import React, { Component } from 'react';
import posed from 'react-pose';
import classNames from 'classnames';
import items from './items';
import style from './navs.module.css';
import SubMenu from './sub-menu/sub-menu';

const Sidebar = posed.div({
  open: {
    y: '0%',
    transition: { duration: 300 },
    delayChildren: 0,
    staggerChildren: 150,
  },
  closed: { y: '-100%', delay: 0, transition: { duration: 300 } },
  initialPose: 'closed',
});

const Item = posed.li({
  open: { opacity: 1 },
  closed: { opacity: 0 },
  smallOpen: { opacity: 1, x: '0%', delay: 200, transition: { duration: 200 } },
  smallClosed: {
    opacity: 0,
    x: '100%',
    transition: { duration: 200 },
  },
  initialPose: 'smallOpen',
});

export default class Navs extends Component {
  state = {
    subOpen: false,
    mainItems: items.slice(1),
    i: null,
  };

  onHandleClick = () =>
    this.setState(prevState => ({ subOpen: !prevState.subOpen }));

  componentWillUpdate = () => {
    if (this.props.open === false) this.setState({ subOpen: false });
  };

  onHover = i => {
    this.setState({ i });
  };

  onMouseOut = () => {
    this.setState({ i: null });
  };

  render() {
    const { open } = this.props;
    const { mainItems, subOpen, i } = this.state;
    const className = classNames(
      'material-icons',
      style.i,
      subOpen ? style.iRotate : '',
    );
    const classNameLink = classNames(
      style.link,
      subOpen ? style.linkActive : '',
    );
    const name = index => {
      switch (index) {
        case 0:
          return style.navsWrapper0;
          break;
        case 1:
          return style.navsWrapper1;
          break;
        case 2:
          return style.navsWrapper2;
          break;
        case 3:
          return style.navsWrapper3;
          break;
        case 4:
          return style.navsWrapper4;
          break;
        default:
          return style.navsWrapper;
      }
    };
    const background = classNames(
      i !== null ? name(i) : '',
      subOpen ? style.navsWrapper4 : '',
    );
    return (
      <Sidebar className={style.navsWrapper} pose={open ? 'open' : 'closed'}>
        <div className={background}>
          <nav className={style.nav}>
            <ul className={style.navs}>
              <Item
                onMouseOver={() => this.onHover(4)}
                onMouseLeave={this.onMouseOut}
                className={style.listItem}
              >
                <button
                  type="button"
                  onClick={this.onHandleClick}
                  className={classNameLink}
                >
                  {items[0]}
                  <i className={className}>arrow_drop_down</i>
                </button>
                <SubMenu subOpen={subOpen} />
              </Item>
              {mainItems.map((item, i) => (
                <Item
                  className={style.listItem}
                  onMouseOver={() => this.onHover(i)}
                  onMouseLeave={this.onMouseOut}
                  key={item}
                  pose={subOpen ? 'smallClosed' : 'smallOpen'}
                >
                  <a href="#" className={style.link}>
                    {item}
                  </a>
                </Item>
              ))}
            </ul>
          </nav>
          <div className={style.contacts}>
            <p>
              <i className="material-icons">call</i>
              <i className="material-icons">near_me</i>
              +38 (063) 278-15-84
            </p>
            <p>
              <i className="material-icons">local_post_office</i>
              ceo@cybercreation.team
            </p>
          </div>
        </div>
      </Sidebar>
    );
  }
}
